// 组织树数据
export const getTreeData = (params) =>
  defHttp.get(
    {
      url: Api.getTreeTypeUrl,
      params,
    },
    {},
  );

// 获取知识库分类
export const getType = (params) =>
  defHttp.get(
    {
      url: Api.getTypeUrl,
      params,
    },
    {},
  );

// 新增知识库分类
export const insertType = (params) =>
  defHttp.post(
    {
      url: Api.createTypeUrl,
      params,
    },
    {},
  );

// 修改知识库分类
export const updateType = (params) =>
  defHttp.put(
    {
      url: Api.updateTypeUrl,
      params,
    },
    {},
  );

export const saveType = (params) => {
  if (!params.id) {
    return insertType(params);
  }
  return updateType(params);
};

// 删除知识库分类
export const deleteType = (params) =>
  defHttp.delete(
    {
      url: Api.deleteTypeUrl,
      params,
    },
    {},
  );

